	import orderList from "@/components/my-order-list/index.vue"
		import { orderListApi } from '../../../common/api/api.js'
	export default {
		components: {
			orderList,
		},
		data() {
			return {
				loadText: {
					loadmore: '轻轻上拉',
					loading: '努力加载中',
					nomore: '粉丝数据加载完毕~'
				},
				loadStatus:"loadmore",
				
				scrollLeft: 0,
				isClickChange: false,
				currentTab: 0,
				menuTabs: [
					{
						name: '全部'
					}, {
						name: '待付款'
					}, {
						name: '待发货'
					}, {
						name: '待收货'
					}, {
						name: '待评价'
					}, {
						name: '售后'
					},
				],
				menuLists: [
					{
						name: '全部',
						list: [1,2,3,4],
						current: 1,
						tabName:"state_new"
					},
					{ 
						name: '待付款',
						list: [1,2,3],
						current: 1,
						tabName:"state_pay"
					},
					{ 
						name: '待发货',
						list: [1,2],
						current: 1,
						tabName:"state_send"
					},
					{
						name: '待收货',
						list: [1],
						current: 1,
						tabName:"state_success"
					},
					{
						name: '待评价',
						list: [],
						current: 1,
						tabName:"state_refund"
					},
					{
						name: '售后',
						list: [],
						current: 1,
						tabName:"state_cancel"
					}
				]
			}
		},
		onLoad(options) {
			// for (var i = 0; i < this.menuLists.length; i++) {
			// 	this.getDateList(i);
			// }
			// 获取上一页面的数据
			this.currentTab = options.type
			// console.log(this.tabIndex)
			// 获取全部状态的列表
			// this.getAllList()
		},
		// 下拉
		// onPullDownRefresh() {
		// 	this.onPullDownHandle()
		// },
		methods: {
			// 下拉刷新
			// onPullDownHandle(){
			// 	this.getAllList()
				
			// 	setTimeout(function () {
			// 	    uni.stopPullDownRefresh();
			// 	}, 1000);
			// },
			getAllList(){},
			swichMenu: async function(current) { //点击其中一个选项
				if (this.currentTab == current) {
					return false;
				} else {
					this.currentTab = current;
					this.setScrollLeft(current);
				}
			},
			swiperChange: async function(e) {
				let index = e.target.current;
				this.setScrollLeft(index);
				this.currentTab = index;
			},
			setScrollLeft: async function(tabIndex) {
				let leftWidthSum = 0;
				for (var i = 0; i <= tabIndex; i++) {
					let nowElement = await this.getWidth('tabNum' + i);
					leftWidthSum = leftWidthSum + nowElement.width;
				}
				let winWidth = uni.getSystemInfoSync().windowWidth;
				this.scrollLeft = leftWidthSum > winWidth ? (leftWidthSum - winWidth) : 0
			},
			getWidth: function(id) { //得到元素的宽高
				return new Promise((res, rej) => {
					uni.createSelectorQuery().select("#" + id).fields({
						size: true,
						scrollOffset: true
					}, (data) => {
						res(data);
					}).exec();
				})
			},
			getDateList: function(tabIndex) {
				// var entity = this.menuTabs[tabIndex].name;
				// this.menuLists[tabIndex].push(entity);
			},
		}
	}
</script>

